body {
  font-family: 'HELVETICA NEUE', 'HELVETICA', 'ARIAL'; }

h1 {
  font-size: 40px; }

h2 {
  font-size: 32px; }

h3 {
  font-size: 28px; }

h4 {
  font-size: 24px; }

h5 {
  font-size: 20px; }

h6 {
  font-size: 18px; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 300;
  margin: 0;
  padding: 0; }

#sidebar {
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  height: 100vh;
  left: 0;
  position: fixed;
  top: 0;
  -webkit-transform: translateX(-500px);
  -ms-transform: translateX(-500px);
  transform: translateX(-500px);
  -webkit-transition: -webkit-transform 0.3s ease-in-out;
  transition: -webkit-transform 0.3s ease-in-out;
  -o-transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  width: 400px;
  z-index: 1000;
  border-right: 4px double rosybrown; }
  #sidebar a {
    margin: 12px;
    padding: 20px;
    font-size: 22px; }
  #sidebar .sidebar-button {
    cursor: pointer;
    border: 1px solid black;
    outline-width: 0; }
  #sidebar .sidebar-button:hover {
    border: 1px dotted black;
    -webkit-box-shadow: 0 0 20px 2px lightgray;
    box-shadow: 0 0 20px 2px lightgray; }
  #sidebar .sidebar-button:active {
    border: 1px inset black;
    -webkit-box-shadow: 0 0 4px 2px gray;
    box-shadow: 0 0 4px 2px gray; }
  #sidebar #sidebar-collapse {
    position: absolute;
    top: 20px;
    right: -40px;
    width: 80px;
    height: 30px;
    background-color: white;
    border-radius: 8px;
    border: 1px solid black; }

@media screen and (orientation: portrait) {
  #sidebar {
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
    -webkit-transform: translateY(calc(100vh + 500px));
    -ms-transform: translateY(calc(100vh + 500px));
    transform: translateY(calc(100vh + 500px));
    width: 100vw;
    height: 500px; }
    #sidebar #sidebar-collapse {
      top: -10px;
      right: 20px; } }



